// Thng bo thu thp cookie
.cookie-notice {
    position: fixed;
    bottom: 1rem;
    left: 1rem;
    width: 22rem;

    @include responsive() {
        max-width: calc(100vw - 2rem);
    }

    z-index: $zindex-cookie-notice;
    background-color: var(--#{$prefix}secondary-bg);
    border: var(--#{$prefix}border-width) var(--#{$prefix}border-style) var(--#{$prefix}border-color);
    border-radius: var(--#{$prefix}border-radius);
    box-shadow: var(--#{$prefix}box-shadow);
    padding: 1rem;

    .btn-close {
        float: right;
        margin-left: .5rem;
        margin-bottom: .5rem;
    }
}

// Thng bo phin ng nhp ht hn
.timeoutsess {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: $zindex-timeoutsess;
    display: none;
}
